.aboutMe {
    background-color: blanchedalmond;
    min-height: 80vh;
}

.contact {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 100%;
    background-color: rgba(147, 138, 138, 0.67);
}

.img {
    width: 350px;
    max-width: 100%;
    height: 450px;
    background-image: url("./../assets/img/avatarka2.jpg");
    background-size: cover;
    background-position: center;
}

.aboutMe h3 {
    text-align: center;
    color: darkslategrey;
}

.contact ul {
    display: flex;
    list-style: none;
    flex-wrap: wrap;
    padding: 0 10px;
    background-color: #c584c5;
}

.contact li {
    padding: 10px;
}

.icon1 {
    width: 50px;
    height: 50px;
    background-image: url("./../assets/svg/icon_telegram.svg");
    background-size: cover;
    background-position: center;
    cursor: pointer;
}

.context {
    background-color: rgba(232, 219, 219, 0.67);
    padding: 0 10px;
    max-width: 700px;
}

.container {
    align-items: start;
}
.myDescription{
    border: 1px solid;
    max-width: 700px;
}
.link{
    width: 150px;
    height: 50px;
    background-color: rgba(159,252,73,0.85);
    text-align: center;
    padding: 10px 0;
    margin: 10px;
    text-decoration-line: none;
}